import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { ICategoryEarning } from 'src/app/interfaces/categories_earnings';
import { IEarning } from 'src/app/interfaces/earnings';
import { IUserData } from 'src/app/interfaces/user';
import { EarningsService } from 'src/app/services/earnings.service';
import { StoreService } from 'src/app/services/store.service';
import { IState } from 'src/app/store';
import { CategoriesEarnings } from 'src/app/store/actions/categories_earnings';

@Component({
  selector: 'app-item-earning',
  templateUrl: './item-earning.component.html'
})
export class ItemEarningComponent implements OnInit, OnDestroy {
  @Input() showDelete: boolean = false;
  @Input() complete: boolean = false;
  @Input() item: IEarning = {};
  @Input() link: boolean = true;
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  showForm: boolean = false;

  categories: Array<ICategoryEarning> = [];
  accounts: IAccount[] = [];
  user: IUserData = {};

  sus: Subscription = new Subscription();

  constructor(
    private earningsSrv: EarningsService,
    private storeSrv: StoreService, 
    private store: Store<IState>,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state: IState) => {
      this.categories = state?.categories_earnings?.data;
      this.accounts = state?.user?.accounts;
      this.user = state?.user?.data;
    });
  }

  async ngOnInit() {
    if(!this.categories?.length){
      try {
        console.log('*********************************');
        console.log('onInit item earning');
        console.log('*********************************');
        let response: any = await this.earningsSrv.categoriesEarnings(JSON.parse(localStorage.user).person.id);

        if(response.length){
          this.store.dispatch(CategoriesEarnings({any: response}));
        }else{
          console.log('error en categories earnings');
        }
      } catch (error) { }
    }
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  seeDetail(): void{
    if(!this.link){
      return;
    }

    this.router.navigate(['/dashboard/earnings/', this.item?.id, 'details']);
  }

  editEarning(): void{
    this.showForm = !this.showForm;
  }

  async deleteEarning(item: IEarning) {
    await this.storeSrv.deleteEarning(item);
  }
}
