import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AccountsService {
    constructor(private http: HttpClient) { }

    getDetailAccount(id: string | number){
        if(!id) return;
        return this.http.get(`${environment.urlBackend}accounts/${id}`).toPromise();
    }
    
    updateAccount(id: string | number, data: any){
        if(!id) return;
        return this.http.put(`${environment.urlBackend}accounts/${id}`, data).toPromise();
    }
    
    deleteAccount(id: string | number){
        if(!id) return;
        return this.http.delete(`${environment.urlBackend}accounts/${id}`).toPromise();
    }
}