import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({ providedIn: 'root' })
export class HeritageService {

  constructor(private http: HttpClient) { }

  createHeritage(data: any){
    return this.http.post(`${environment.urlBackend}create-heritage`, data).toPromise();
  }

  heritages(id: string){
    if(!id) return;
    return this.http.get(`${environment.urlBackend}heritage-person/${id}`).toPromise();
  }

  deleteHeritage(data: any){
    return this.http.post(`${environment.urlBackend}delete-heritage`, data).toPromise();
  }

  updateStates(ids: any){
    return this.http.put(`${environment.urlBackend}update-heritage-state`, ids).toPromise();
  }

  searchHeritages(data: any){
    if(!data.person) return;
    const url = `${environment.urlBackend}heritage-search?person=${data.person}${data.search ? `&search=${data.search}` : ''}${data.active !== null ? `&active=${data.active}` : ''}`.trim();
    return this.http.get(url, data).toPromise(); }

}
