import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { IUserData } from 'src/app/interfaces/user';
import { AccountsService } from 'src/app/services/accounts.service';
import { StoreService } from 'src/app/services/store.service';
import { UserService } from 'src/app/services/user.service';
import { IState } from 'src/app/store';
import { Accounts } from 'src/app/store/actions/user';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-list-accounts',
  templateUrl: './list-accounts.component.html'
})
export class ListAccountsComponent implements OnInit {
  @Input() limit: number = 100;
  @Input() cols: number = 1;
  @Input() button: boolean = true;
  @Input() basicMenu: boolean = false;
  @Input() sidebar: boolean = true;
  @Input() showOrder: boolean = true;
  @Input() showDelete: boolean = true;
  @Input() padding: boolean = true;
  @Input() showAllLink: boolean = false;

  user$ = this.store.select(state => state.user.data);
  us: IUserData = {};

  sus: Subscription = new Subscription();

  totalActives: number = 0;
  totalInactives: number = 0;
  totalProtected: number = 0;
  totalSavings: number = 0;
  totalSavingsA: number = 0;
  totalCredit: number = 0;
  totalCreditA: number = 0;
  totalAmountCredit: number = 0;
  totalAmountCreditA: number = 0;

  showInactives: boolean = false;

  constructor(
    private store: Store<IState>,
    private userService: UserService,
    private accountSrv: AccountsService,
    private storeSrv: StoreService,
    public router: Router
  ) {
    this.sus = this.store.select((state) => state.user.data).subscribe((data) => {
      this.us = data;
    });
  }

  togleInactives(){
    this.showInactives = !this.showInactives;
  }

  async ngOnInit() {
    try {
      let response: any = [];
      if(!this.storeSrv.validateAccounts()){
        response = await this.userService.accounts(''+this.us?.person?.id);
      }else{
        response = this.storeSrv.accounts;
      }
  
      if(response.length){
        this.store.dispatch(Accounts({accounts: [...response]}));

        if(this.sidebar){
          // calculate total actives and inactives
          this.totalActives = [...response].filter((a:IAccount) => a.status).length;
          this.totalInactives = [...response].filter((a:IAccount) => !a.status).length;
          this.totalProtected = [...response].filter((a:IAccount) => a.protected).length;
          this.totalSavings = [...response].filter((a:IAccount) => a.savings).length;
          this.totalSavingsA = [...response].filter((a:IAccount) => a.savings && a.status).length;
          this.totalCredit = [...response].filter((a:IAccount) => a.credit).length;
          this.totalCreditA = [...response].filter((a:IAccount) => a.credit && a.status).length;
  
          [...response].filter(
              (a:any) => {
                if(a.credit && a.quota){
                  this.totalAmountCredit += parseInt(a?.quota);
                  if(a.status){
                    this.totalAmountCreditA += parseInt(a?.quota);
                  }
                }
              });
        }
      }else{
        this.store.dispatch(Accounts({accounts: []}));
      }
    } catch (error) {
      console.log(error);
      this.store.dispatch(Accounts({accounts: []}));
    }
  }

  resetTotals(){
    this.totalActives = 0;
    this.totalInactives = 0;
    this.totalProtected = 0;
    this.totalSavings = 0;
    this.totalSavingsA = 0;
    this.totalCredit = 0;
    this.totalCreditA = 0;
    this.totalAmountCredit = 0;
    this.totalAmountCreditA = 0;
  }

  changeOrder(item: any){
    Swal.fire({
      title: 'Change Order: ' + item?.name,
      input: 'number',
      inputValue: item?.order,
      showCancelButton: true,
      confirmButtonText: 'Save',
      showLoaderOnConfirm: true,
      preConfirm: async (login: any) => {
        const id: any = item?.id;
        const dataToSend: any = {...item};
        if(dataToSend?.id) delete dataToSend?.id;
        const data = {
          ...dataToSend, 
          order: parseInt(login)
        };

        try {
          let res: any = await this.accountSrv.updateAccount(id, data);
          if(res && res?.status){
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Account updated successfully',
              footer: `New Order: ${login}`
            });
            this.store.dispatch(Accounts({accounts: []}));
            this.resetTotals();
          }
        } catch (error) {
          Swal.showValidationMessage(
              `Request failed: ${error}`
            )
        }
      }
    }).then((result) => {
      if (result.isConfirmed) {
        this.resetTotals();
        this.ngOnInit();
      }
    })
  }

  delete(item: any){
    Swal.fire({
      title: 'Delete ' + item?.name + '?',
      input: 'checkbox',
      inputValue: 'true',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      showLoaderOnConfirm: true,
      preConfirm: async (login: any) => {
        if(!login) return;
        const id: any = item?.id;

        try {
          let res: any = await this.accountSrv.deleteAccount(id);
          if(res && res?.status){
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Account deleted successfully',
            });
            this.store.dispatch(Accounts({accounts: []}));
            this.resetTotals();
          }
        } catch (error:any) {
          console.log(error);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: `Error: ${error?.error?.join(', ')}`,
            footer: 'Account could not be deleted',
          });
        }
      }
    }).then((result) => {
      if (result.isConfirmed) {
        this.resetTotals();
        this.ngOnInit();
      }
    })
  }

}
