import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent } from './dashboard.component';
import { StartComponent } from './start/start.component';
import { ProfileComponent } from './profile/profile.component';
import { DashboardService } from 'src/app/guards/dashboard.service';
import { Page404Component } from './404/404.component';

const routes: Routes = [
  { 
    path: '', component: DashboardComponent,
    children: [
      // Start Page
      { path: '', component: StartComponent },
      
      // Finances 
      { path: 'investments', loadChildren: () => import('../dashboard/investments/investments.module').then(m => m.InvestmentsModule), canActivate: [DashboardService], },
      { path: 'heritage', loadChildren: () => import('../dashboard/heritage/heritage.module').then(m => m.HeritageModule), canActivate: [DashboardService], },
      { path: 'expensives', loadChildren: () => import('../dashboard/expensives/expensives.module').then(m => m.ExpensivesModule), canActivate: [DashboardService], },
      { path: 'earnings', loadChildren: () => import('../dashboard/earnings/earnings.module').then(m => m.EarningsModule), canActivate: [DashboardService], },
      { path: 'accounts', loadChildren: () => import('../dashboard/accounts/accounts.module').then(m => m.AccountsModule), canActivate: [DashboardService], },

      // profile
      { path: 'profile', component: ProfileComponent },

      // Vehicles
      { path: 'vehicles', loadChildren: () => import('../dashboard/vehicles/vehicles.module').then(m => m.VehiclesModule), canActivate: [DashboardService], },

      // 404
      { path: '**', redirectTo: '404' }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
