import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Actions } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IUserData } from 'src/app/interfaces/user';
import { HeritageService } from 'src/app/services/heritage.service';
import { IState } from 'src/app/store';
import { HeritagePerson } from 'src/app/store/actions/user';

export interface IColsWrap{
  xl?: number;
  md?: number;
  sm?: number;
}

@Component({
  selector: 'app-heritage-person',
  templateUrl: './heritage-person.component.html',
  styles: [],
})
export class HeritagePersonComponent implements OnInit, OnDestroy {
  @Input() showHeader: boolean = true;
  @Input() showLink: boolean = true;
  @Input() showDelete: boolean = false;
  @Input() padding: boolean = true;
  @Input() limit: number = 20;
  @Input() cols: IColsWrap = {
    xl: 4,
    md: 3,
    sm: 2,
  };
  @Input() updateState: boolean = false;

  user:IUserData = {};
  heritages: Observable<any> = this.store.select((state) => state.user.heritages);
  susA: Subscription = new Subscription();
  sub: Subscription = new Subscription();

  constructor(
    private HeritageService: HeritageService, 
    private store: Store<IState>,
    private actions: Actions<IState>
  ) {
    this.susA = this.actions.subscribe((action: any) => {
      // console.log('action', action);
      if(action.type === '[User] UpdateStatesHeritage'){
        this.ngOnInit();
      }
    });

    this.sub = this.store.select(state => state).subscribe(data => {
      try {
        this.user = data?.user?.data;
      } catch (error) { console.log(error); }
    })
  }

  ngOnDestroy(): void {
    this.susA.unsubscribe();
    this.sub.unsubscribe();
  }

  async ngOnInit() {
    try {
      let response: any = await this.HeritageService.heritages((this.user?.person?.id ? ''+this.user?.person?.id:''));

      if (response.heritages.length) {
        this.store.dispatch(HeritagePerson({ heritages: response }));
      }else {
        this.store.dispatch(HeritagePerson({ heritages: {heritages: [], total: 0} }));
      }
    } catch (error) {
      this.store.dispatch(HeritagePerson({ heritages: {heritages: [], total: 0} }));
      // console.log(error);
    }
  }

  async deleteHeritage(item: any) {
    // return;
    if (item.id) {
      try {
        let data = {
          heritage: item.id,
          person: item.id_person,
        };
        let response: any = await this.HeritageService.deleteHeritage(data);

        if (response.heritage) {
          // console.log('patrimonio eliminado exitosamente');
          this.store.dispatch(HeritagePerson({ heritages: response.heritage }));
        } else {
          // console.log('patrimonio eliminado fallida');
        }
      } catch (error) {
        // console.log('error => ', error);
      }
    }
  }
}