import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { InvestmentsService } from 'src/app/services/investments.service';
import { Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { IUserData } from 'src/app/interfaces/user';
import { IState } from 'src/app/store';
import { ICategoryInvestment } from 'src/app/interfaces/categories_investments';
import { StoreService } from 'src/app/services/store.service';
import { IInvestment } from 'src/app/interfaces/investments';

@Component({
  selector: 'app-item-investment',
  templateUrl: './item-investment.component.html'
})
export class ItemInversionComponent implements OnInit, OnDestroy {
  @Input() showDelete: boolean = false;
  @Input() complete: boolean = false;
  @Input() item: any;
  @Input() link: any = true;
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  showForm: boolean = false;

  categories: Array<ICategoryInvestment> = [];
  accounts: IAccount[] = [];
  user: IUserData = {};

  form: FormGroup = new FormGroup({});
  sus: Subscription = new Subscription();

  constructor( 
    private investmentService: InvestmentsService, 
    private store: Store<IState>,
    private router: Router,
    private storeSrv: StoreService, 
  ) {
    this.sus = this.store.subscribe((state: IState) => {
      this.categories = state?.categories_investments?.data;
      this.accounts = state?.user?.accounts;
      this.user = state?.user?.data;
    });
  }

  ngOnInit() {
    this.form = new FormGroup({
      value: new FormControl(this.item?.value, Validators.required),
      description: new FormControl(this.item?.description, Validators.required),
      id_category_investments: new FormControl(this.item?.category_investment?.id, Validators.required),
      id_account: new FormControl(this.item?.account?.id, Validators.required),
      date: new FormControl(this.item?.date, Validators.required)
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  seeDetail(): void{
    if(!this.link){
      return;
    }

    this.router.navigate(['/dashboard', 'investments', this.item.id, 'details']);
  }

  editInvestment(item: any): void{
    this.showForm = !this.showForm;
  }

  async deleteInvestment(item: IInvestment) {
    await this.storeSrv.deleteInvestment(item);
  }

}
