import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { IState } from 'src/app/store';
import { LogOut } from 'src/app/store/actions/user';
import { INavLi, dataNav } from './data.nav';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html'
})
export class NavComponent implements OnInit {
  user: Observable<any> = this.store.select(state => state.user.data);
  isOpenMenu: boolean = false;
  isStaticMenu: boolean = false;

  navData: INavLi[] = dataNav; 

  constructor(private store: Store<IState>, private router:Router) { }

  ngOnInit() {
  }

  logout(){
    this.store.dispatch(LogOut());
    this.router.navigateByUrl('/login');
  }

  nav(ref?: string){
    if(ref == 'open' && !this.isStaticMenu && !localStorage.isStaticMenu){
      this.isOpenMenu = true;
    }else if(ref === 'close' && !this.isStaticMenu && !localStorage.isStaticMenu){
      this.isOpenMenu = false;
    }else if(!localStorage.isStaticMenu){
      if(!this.isStaticMenu){
        this.isOpenMenu = !this.isOpenMenu;
      }else{
        this.isOpenMenu = true;
      }
    }

    localStorage.isOpenMenu = JSON.stringify(this.isOpenMenu);
  }
}
