import { Component, Input, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { HeritageService } from 'src/app/services/heritage.service';
import { IState } from 'src/app/store';
import { HeritagePerson } from 'src/app/store/actions/user';

@Component({
  selector: 'app-item-heritage',
  templateUrl: './item-heritage.component.html'
})
export class ItemHeritageComponent implements OnInit {
  @Input() showDelete: boolean = false;
  @Input() item: any;
  @Input() updateState: boolean = false;

  constructor(private patrimonioSrv: HeritageService, private store: Store<IState>) { }

  ngOnInit() {
  }

  async deleteHeritage(item: any) {
    // return;
    if (item.id) {
      try {
        let data = {
          heritage: item.id,
          person: item.id_person,
        };
        let response: any = await this.patrimonioSrv.deleteHeritage(data);

        if (response.heritage) {
          console.log('heritage eliminado exitosamente');
          this.store.dispatch(HeritagePerson({ heritages: response.heritages }));
        } else {
          console.log('patrimonio eliminado fallida');
        }
      } catch (error) {
        console.log('error => ', error);
      }
    }
  }

  updateData(e: any, item: any){
    const flag: boolean = e.target.checked;
    let itemsLocal:any[] = (localStorage?.itemsStatesHeritage ? JSON.parse(localStorage?.itemsStatesHeritage) : []) || [];


    if(!flag || !item.active){
      if(flag || item.active){
        itemsLocal.push({
          id: item.id,
          state: !item.active
        });
      }else{
        itemsLocal = itemsLocal.filter(i => i.id !== item.id);
      }
    }else{
      itemsLocal = itemsLocal.filter(i => i.id !== item.id);
    }

    localStorage.itemsStatesHeritage = JSON.stringify(itemsLocal);
  }

}
