import { Location } from '@angular/common';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Actions } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { ICategoryInvestment } from 'src/app/interfaces/categories_investments';
import { IStoreInvestments } from 'src/app/interfaces/investments';
import { IUserData } from 'src/app/interfaces/user';
import { UtilsInvestmentsService } from 'src/app/pages/dashboard/investments/services/utils-investments.service';
import { IState } from 'src/app/store';
import { resetPageFilters } from 'src/app/store/actions/investments.actions';

@Component({
  selector: 'app-investments-filters',
  templateUrl: './investments-filters.component.html'
})
export class InvestmentsFiltersComponent implements OnInit, OnDestroy {
  @Input() form: FormGroup = new FormGroup({});
  @Input() isFromCreate: boolean = false;
  @Input() isFromGraphics: boolean = false;

  sub: Subscription = new Subscription();
  subA: Subscription = new Subscription();

  user:IUserData = {};

  quantity$: Observable<number> = this.store.select((state) => state.investments.quantity);
  investments$: Observable<IStoreInvestments> = this.store.select((state) => state.investments);
  categories: Observable<ICategoryInvestment[]> = this.store.select(state => state.categories_investments.data);
  accounts: Observable<IAccount[]> = this.store.select(state => state.user.data.accounts);

  loading: boolean = false;
  loaded: boolean = false;

  queryParams: any;
  params: any;

  arrayItems: number[] = [4,8,12,16,20,24,28,36,48,60,100];
  arrayKeys: any[] = [
    {value: 'date', text: 'Date'},
    {value: 'value', text: 'Value'},
    {value: 'description', text: 'Description'},
    {value: 'id_category_investments', text: 'Category'},
    {value: 'id_account', text: 'Account'},
  ];
  arrayTypes: any[] = [
    {value: 'DESC', text: 'Desc'},
    {value: 'ASC', text: 'Asc'},
  ];

  constructor(
    private store: Store<IState>,
    private utils: UtilsInvestmentsService,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private actions: Actions<IState>
  ) {
    this.location.subscribe(async (x) => {
      if(x?.pop){
        await this.investmentsFilter();
      }
    });
    this.sub = this.store.select(state => state).subscribe(data => {
      try {
        this.user = data?.user?.data;
        this.queryParams = data?.investments?.filters;
        this.loading = data?.investments?.loading;
        this.loaded = data?.investments?.loaded;
      } catch (error) {}
    });

    this.subA = this.actions.subscribe((action: any) => {
      // console.log('action', action);
      if(
        action.type === '[Investment User] Set Filters Initial' || 
        action.type === '[Investment User] Restore Filters Investments'
      ){
        this.utils.setDataOnForm(this.form, this.queryParams);
      }

      if(action.type === '[Investment User] Reset Filters Investments'){
        this.form = this.utils.form();
      }

      if(action.type === '[Investment User] Update Filters Paginator Investments'){
        this.investmentsFilter(false);
      }
    });
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
    this.subA.unsubscribe();
  }

  async ngOnInit() {}

  async investmentsFilter(reset: boolean = true) {
    console.warn(this.form.value);
    this.params = { ...this.queryParams, };

    if(this.queryParams.page > 0 && reset){
      this.store.dispatch(resetPageFilters())
      this.params.page = 0;
      this.params.pages = 0;
      this.params.pagesArray = [];
    }

    this.utils.investmentsFilter(this.params, this.activatedRoute, this.form);
  }
}
