import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { ICategoryEarning } from 'src/app/interfaces/categories_earnings';
import { IEarning } from 'src/app/interfaces/earnings';
import { ICreateEarningsResponse } from 'src/app/interfaces/http/earnings-http.type';
import { IUserData } from 'src/app/interfaces/user';
import { EarningsService } from 'src/app/services/earnings.service';
import { IState } from 'src/app/store';
import { CreateEarningSuccess, EarningsReset } from 'src/app/store/actions/earnings.actions';
import { currentDateInput } from 'src/environments/global';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-form-earnings',
  templateUrl: './form-earnings.component.html'
})
export class FormEarningsComponent implements OnInit, OnDestroy {
  @Input() earning: IEarning = {};
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  form: FormGroup = new FormGroup({});
  date: string = currentDateInput();
  user: IUserData = {};
  categories: ICategoryEarning[] = [];
  categories$: Observable<ICategoryEarning[]> = this.store.select((state) => state.categories_earnings.data);

  sus: Subscription = new Subscription();

  constructor(
    private earningsSrv: EarningsService, 
    private store: Store<IState>,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state) => {
      this.categories = state.categories_earnings.data;
      this.user = state.user.data;
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  ngOnInit() {
    this.form = new FormGroup({
      id: new FormControl(this.earning?.id),
      value: new FormControl(this.earning?.value, Validators.required),
      description: new FormControl(this.earning?.description, Validators.required),
      id_category_earnings: new FormControl(this.earning?.category_earnings?.id, Validators.required),
      id_account: new FormControl(this.earning?.account?.id, Validators.required),
      date: new FormControl(this.earning?.date || this.date, Validators.required)
    });

    setTimeout(() => this.value?.nativeElement.focus(), 300);
  }

  async createOrUpdateEarning() {
    if (this.form.valid) {
      let { date } = this.form.value;

      try {
        let data = {
          ...this.form.value,
          date: new Date(date),
          id_person: this.user?.person?.id,
        };
        let response: ICreateEarningsResponse = await this.earningsSrv.createOrUpdateEarnings(''+this.earning.id, data);

        if (response.earning) {
          if(!this.earning.id){
            // console.log('registro exitoso');
            this.form.reset();
            this.form.controls.date.setValue(this.date);
  
            this.value?.nativeElement.focus();
  
            if(response.earnings){
              Swal.fire('Earning Created Succesfully!', '', 'success');
              this.store.dispatch(CreateEarningSuccess());
            }
          }else{
            this.store.dispatch(EarningsReset());
            if(response?.status === 1) {
              Swal.fire('Earning Updated Succesfully!', '', 'success')
            };
            this.router.navigateByUrl('/dashboard/earnings');
          }
        } else {
          // console.log('registro fallido');
        }
      } catch (error) {
        console.log('error => ', error);
      }
    }
  }

  validateSelect(ref: string){
    const id_category_earnings: string = this.form.value[ref];
    const url: any = ref === 'id_category_earnings' ? '/dashboard/earnings/categories/create' : '/dashboard/accounts/create';
    if(id_category_earnings === 'create' && !this.categories?.length){
      this.router.navigateByUrl(url);
    }
  }

}
