import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IState } from 'src/app/store';
import { IColsWrap } from '../heritage-person/heritage-person.component';
import { IEarning, IStoreEarnings } from 'src/app/interfaces/earnings';
import { IUserData } from 'src/app/interfaces/user';
import { ActivatedRoute, Router } from '@angular/router';
import { StoreService } from 'src/app/services/store.service';
import { ResetFiltersEarnings, RestoreFiltersEarnings, updateFiltersPaginatorEarnings } from 'src/app/store/actions/earnings.actions';
import { UtilsEarningsService } from 'src/app/pages/dashboard/earnings/services/utils-earnings.service';

@Component({
  selector: 'app-earnings-person',
  templateUrl: './earnings-person.component.html'
})
export class EarningsPersonComponent implements OnInit, OnDestroy {
  @Input() showHeader: boolean = true;
  @Input() padding: boolean = true;
  @Input() showLink: boolean = true;
  @Input() showDelete: boolean = false;
  @Input() limit: number = 0;
  @Input() cols: IColsWrap = {
    xl: 4,
    md: 3,
    sm: 2,
  };
  user: IUserData = {};

  pages: number = 0;
  pagesArray: any[] = [];
  limit$: number = 0;
  page: number = 0;
  pageA: number = 0;
  quantity: number = 0;
  earnings$: Observable<IStoreEarnings> = this.store.select((state) => state.earnings);
  loading: boolean = false;
  loaded: boolean = false;

  sus: Subscription = new Subscription();
  susA: Subscription = new Subscription();

  paramsRouter: any = {};
  params: any = {};
  validUrl: boolean = true;

  constructor(
    private store: Store<IState>,
    private storeSrv: StoreService,
    private utils: UtilsEarningsService,
    private router: Router,
    private actRoute: ActivatedRoute
  ) {
    if(this.router.url.includes('create')) {
      this.validUrl = false;
    }

    this.susA = this.actRoute.queryParams.subscribe(params => {
      // console.warn('suscription params EARNINGS PERSON', params);
      // this.params = params;
      if(Object.keys(params).length){
        // console.log('suscription (store) earnings-person.component', params);
        this.paramsRouter = params;
        // this.createUrl(params);
      }
    });

    this.sus = this.store.select((state) => state).subscribe((state) => {
      this.user = state?.user?.data;
      this.loading = state?.earnings?.loading;
      this.loaded = state?.earnings?.loaded;
      this.page = state?.earnings?.filters?.page;
      this.pageA = state?.earnings?.filters?.page;
      this.pages = state?.earnings?.filters?.pages;
      this.pagesArray = state?.earnings?.filters?.pagesArray;
      this.limit$ = state?.earnings?.filters?.limit;
      this.quantity = state?.earnings?.filters?.quantity;
      
      if(Object.keys(state?.earnings?.filters).length){
        // console.log('suscription (store) earnings-person.component', state?.earnings?.filters);
        this.params = state?.earnings?.filters;
        // this.createUrl(state?.earnings?.filters);
      }
    });
  }

  async getAllData() {
    await this.storeSrv.getAllDataEarnings();
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
    this.susA.unsubscribe();

    this.store.dispatch(ResetFiltersEarnings());
  }

  async ngOnInit() {
    if(Object.keys(this.paramsRouter).length){
      // console.log(this.paramsRouter);
      this.store.dispatch(RestoreFiltersEarnings({ filters: this.paramsRouter }));
    }
    await this.getAllData();
    try {
      if(this.router.url.indexOf('earnings/create') >= 0 && Object.keys(this.params).length){
        return;
      } else if(!this.router.url.includes('create')){
        await this.storeSrv.earningsByDate(this.paramsRouter, 'onInitPersonEar');
        return;
      }else{
        await this.storeSrv.earningsByDate({}, '---onInitPersonEarElse--');
      }
    } catch (error) {
      // this.store.dispatch(EarningsPersonSuccess({ earnings: [], ref: 'ngOnInitCatch EarningsPerson' }));
      // console.log(error);
    }
  }

  async deleteEarning(item: IEarning) {
    await this.storeSrv.deleteEarning(item);
  }

  createUrl(params: any){
    this.utils.createUrl(params, this.actRoute, 'earnings-person.component');
  }

  setPage(): void {
    this.store.dispatch(updateFiltersPaginatorEarnings({
      filters: { page: this.pageA },
      ref: 'setPage'
    }));
  }

  next(): void {
    this.page++;
    this.store.dispatch(updateFiltersPaginatorEarnings({
      filters: { page: this.page },
      ref: 'next'
    }));
  }

  prev(): void {
    this.page--;
    this.store.dispatch(updateFiltersPaginatorEarnings({
      filters: { page: this.page },
      ref: 'prev'
    }));
  }
}