import { Component, Input, OnInit } from '@angular/core';
import { ApexTitleSubtitle, ApexXAxis } from 'ng-apexcharts';
import { TType } from 'src/app/interfaces/charts';
import { GlobalService } from 'src/app/services/global.service';

@Component({
  selector: 'app-donut',
  templateUrl: './donut.component.html'
})
export class DonutComponent implements OnInit {
  @Input() dataGraphics: any = [];
  @Input() id: string = '' + Math.floor(Math.random() * 1000);
  @Input() type: TType = 'account';
  @Input() title: ApexTitleSubtitle= {};
  @Input() subtitle: ApexTitleSubtitle= {};
  @Input() nameLegend: string = '';
  @Input() nameAvg: string = '';
  @Input() height: number = 800;
  @Input() xAxis: ApexXAxis = {};

  public chartOptions: any;
  dataChart: any = [];

  constructor(private globalSrv: GlobalService) { }

  ngOnInit() {
    this.dataChart = (this.type === 'account'? 
            this.globalSrv.generateDataDonut(this.nameLegend, this.dataGraphics, 'account') : (
              this.type === 'categoryexpensives' ?
                this.globalSrv.generateDataDonut(this.nameLegend, this.dataGraphics, 'expensives') : (
                  this.type === 'categoryearnings' ?
                    this.globalSrv.generateDataDonut(this.nameLegend, this.dataGraphics, 'earnings') : (
                      this.type === 'categoryinvestments' ?
                        this.globalSrv.generateDataDonut(this.nameLegend, this.dataGraphics, 'investment') : []
                    )
                  )
              )
            );

    const defaultValuesLine: any = {
      series: [...this.dataChart].map((i: any) => i.y),
      labels: [...this?.dataChart].map((i: any) => i.x +' - $'+new Intl.NumberFormat().format(i.y)),
      title: this.title,
      height: this.height,
      nameAnnotation: this.nameAvg,
    };

    this.chartOptions = (
        this.type === 'account' ||
        this.type === 'categoryexpensives' ||
        this.type === 'categoryearnings' ||
        this.type === 'categoryinvestments'
          ? 
            this.globalSrv.dataChartDonut(
              defaultValuesLine, 
              this.dataChart?.average,
            ) : []
          );
  }

}
