import { Component, ContentChild, Input, OnInit, TemplateRef } from '@angular/core';

@Component({
  selector: 'app-btn',
  template: `
    <ng-container *ngIf="isLink && !isBtn">
      <a class="capitalize text-md hover:no-underline" [class]="classes" [routerLink]="routes" routerLinkActive="router-link-active" [title]="title">
        <ng-container [ngTemplateOutlet]="fill"></ng-container>
      </a>
    </ng-container>
    
    <ng-container *ngIf="!isLink && isBtn">
      <ng-container [ngTemplateOutlet]="fill"></ng-container>
    </ng-container>
    
    <ng-container *ngIf="!isLink && !isBtn">
      <ng-container [ngTemplateOutlet]="fill"></ng-container>
    </ng-container>
  `
})
export class BtnComponent implements OnInit {
  @Input() isLink: boolean = true;
  @Input() isBtn: boolean = false;
  @Input() routes: string[] | string = '/';
  @Input() title: string = '';
  @Input() classes: string = '';
  @ContentChild('fill') fill: TemplateRef<any> | any;
  
  @Input() linkComplete: string = '';
  
  constructor() { }

  ngOnInit() {
  }

}
