import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IInvestment } from 'src/app/interfaces/investments';
import { IUserData } from 'src/app/interfaces/user';
import { IState } from 'src/app/store';
import { currentDateInput } from 'src/environments/global';
import { ICategoryInvestment } from 'src/app/interfaces/categories_investments';
import { InvestmentsService } from 'src/app/services/investments.service';
import { ICreateInvestmentsResponse } from 'src/app/interfaces/http/investments-http.type';
import { CreateInvestmentSuccess, InvestmentsReset } from 'src/app/store/actions/investments.actions';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-form-investments',
  templateUrl: './form-investments.component.html'
})
export class FormInvestmentsComponent implements OnInit, OnDestroy {
  @Input() investment: IInvestment = {};
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  form: FormGroup = new FormGroup({});
  date: string = currentDateInput();
  user: IUserData = {};
  categories: ICategoryInvestment[] = [];
  categories$: Observable<ICategoryInvestment[]> = this.store.select((state) => state.categories_investments.data);

  sus: Subscription = new Subscription();

  constructor(
    private investmentsSrv: InvestmentsService, 
    private store: Store<IState>,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state) => {
      this.categories = state.categories_investments.data;
      this.user = state.user.data;
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  ngOnInit() {
    this.form = new FormGroup({
      id: new FormControl(this.investment?.id),
      value: new FormControl(this.investment?.value, Validators.required),
      description: new FormControl(this.investment?.description, Validators.required),
      id_category_investments: new FormControl(this.investment?.category_investment?.id, Validators.required),
      id_account: new FormControl(this.investment?.account?.id, Validators.required),
      date: new FormControl(this.investment?.date || this.date, Validators.required)
    });

    setTimeout(() => this.value?.nativeElement.focus(), 300);
  }

  async createOrUpdateInvestment() {
    if (this.form.valid) {
      let { date } = this.form.value;

      try {
        let data = {
          ...this.form.value,
          date: new Date(date),
          id_person: this.user?.person?.id,
        };
        let response: ICreateInvestmentsResponse = await this.investmentsSrv.createUpdateInvestment(''+this.investment.id, data);

        if (response.investment) {
          if(!this.investment.id){
            // console.log('registro exitoso');
            this.form.reset();
            this.form.controls.date.setValue(this.date);
  
            this.value?.nativeElement.focus();
  
            if(response.investments){
              Swal.fire('Investment Created Succesfully!', '', 'success');
              this.store.dispatch(CreateInvestmentSuccess());
            }
          }else{
            this.store.dispatch(InvestmentsReset());
            if(response?.status === 1) {
              Swal.fire('Investment Updated Succesfully!', '', 'success')
            };
            this.router.navigateByUrl('/dashboard/investments');
          }
        } else {
          // console.log('registro fallido');
        }
      } catch (error) {
        console.log('error => ', error);
      }
    }
  }

  validateSelect(ref: string){
    const id_category_investments: string = this.form.value[ref];
    const url: any = ref === 'id_category_investments' ? '/dashboard/investments/categories/create' : '/dashboard/accounts/create';
    if(id_category_investments === 'create' && !this.categories?.length){
      this.router.navigateByUrl(url);
    }
  }

}
