import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { IUserPerson } from 'src/app/interfaces/user';
import { ProfileService } from 'src/app/services/profile.service';
import { IState } from 'src/app/store';
import { SetDataPerson } from 'src/app/store/actions/user';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html'
})
export class ProfileComponent implements OnInit {
  form: FormGroup = new FormGroup({});

  sus: Subscription = new Subscription();
  user: IUserPerson = {};
  id_user: string = '';

  constructor(
    private profileSrv: ProfileService,
    private store: Store<IState>
  ) { 
    this.sus = this.store.subscribe((state: IState) => {
      this.user = state.user?.data?.person;
      this.id_user = state.user?.data?.id;
    });
  }

  async ngOnInit() {
    this.buildForm ();
  }

  buildForm(): void{
    this.form = new FormGroup({
      id: new FormControl(this.user?.id||''),
      fullname: new FormControl(this.user?.fullname||'', Validators.required),
      bio: new FormControl(this.user?.bio||''),
      nit: new FormControl(this.user?.nit||'', Validators.required),
      cellphone: new FormControl(this.user?.cellphone||'', Validators.required),
      email: new FormControl(this.user?.email||''),
      id_user: new FormControl(this.id_user)
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  async setDataProfile(){    
    if(this.form.valid){
      try {
        let data:any = {
          ...this.form.value
        };

        let response: any = await this.profileSrv.setDataProfile(data);

        if(response?.data){
          if(response?.status === 1) Swal.fire('Data Updated Succesfully!', '', 'success');
          this.store.dispatch(SetDataPerson({ any: response.data }));
          this.buildForm();
        }else{
          console.log('Server error', response);
          Swal.fire('Server error', '', 'error');
        }
      } catch (error) {
        Swal.fire('Server error', '', 'error');
        console.log('error => ', error);
      }

    }
  }
}
