import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IState } from 'src/app/store';
import { ResetFiltersExpensives, RestoreFiltersExpensives, updateFiltersPaginatorExpensives } from 'src/app/store/actions/expensives.actions';
import { IColsWrap } from '../heritage-person/heritage-person.component';
import { IExpensive, IStoreExpensives } from 'src/app/interfaces/expensives';
import { IUserData } from 'src/app/interfaces/user';
import { ExpensivesService } from 'src/app/services/expensives.service';
import { StoreService } from 'src/app/services/store.service';
import { UtilsExpensivesService } from 'src/app/pages/dashboard/expensives/services/utils-expensives.service';
import { ActivatedRoute, Router } from '@angular/router';
const notincategory = '61';

@Component({
  selector: 'app-expensives-person',
  templateUrl: './expensives-person.component.html',
})
export class ExpensivePersonComponent implements OnInit, OnDestroy {
  @Input() showHeader: boolean = true;
  @Input() showLink: boolean = true;
  @Input() padding: boolean = true;
  @Input() showDelete: boolean = false;
  @Input() listMode: boolean = false;
  @Input() limit: number = 0;
  @Input() cols: IColsWrap = {
    xl: 4,
    md: 3,
    sm: 2,
  };
  user: IUserData = {};
  pages: number = 0;
  pagesArray: any[] = [];
  limit$: number = 0;
  page: number = 0;
  pageA: number = 0;
  quantity: number = 0;
  loading: boolean = false;
  loaded: boolean = false;
  expensives$: Observable<IStoreExpensives> = this.store.select((state) => state.expensives);

  sus: Subscription = new Subscription();
  susA: Subscription = new Subscription();

  paramsRouter: any = {};
  params: any = {};
  validUrl: boolean = true;

  constructor(
    private expensivesService: ExpensivesService, 
    private store: Store<IState>,
    private storeSrv: StoreService,
    private utils: UtilsExpensivesService,
    private router: Router,
    private actRoute: ActivatedRoute
  ) {
    if(this.router.url.includes('create')) {
      this.validUrl = false;
    }

    this.susA = this.actRoute.queryParams.subscribe(params => {
      // console.warn('suscription params EARNINGS PERSON', params);
      // this.params = params;
      if(Object.keys(params).length){
        // console.log('suscription (store) earnings-person.component', params);
        this.paramsRouter = params;
        // this.createUrl(params);
      }
    });

    this.sus = this.store.select((state) => state).subscribe((state) => {
      this.user = state?.user?.data;
      this.loading = state?.expensives?.loading;
      this.loaded = state?.expensives?.loaded;
      this.page = state?.expensives?.filters?.page;
      this.pageA = state?.expensives?.filters?.page;
      this.pages = state?.expensives?.filters?.pages;
      this.pagesArray = state?.expensives?.filters?.pagesArray;
      this.limit$ = state?.expensives?.filters?.limit;
      this.quantity = state?.expensives?.filters?.quantity;
      
      if(Object.keys(state?.expensives?.filters).length){
        // console.log('suscription (store) expensives-person.component', state?.expensives?.filters);
        this.params = state?.expensives?.filters;
        // this.createUrl(state?.expensives?.filters);
      }
    });
  }

  async ngOnInit() {
    if(Object.keys(this.paramsRouter).length){
      // console.log(this.paramsRouter);
      this.store.dispatch(RestoreFiltersExpensives({ filters: this.paramsRouter }));
    }
    await this.getAllData();
    try {
      if(this.router.url.indexOf('expensives/create') >= 0 && Object.keys(this.params).length){
        return;
      } else if(!this.router.url.includes('create')){
        await this.storeSrv.expensivesByDate(this.paramsRouter, 'onInitPersonEar');
        return;
      }else{
        await this.storeSrv.expensivesByDate({}, '---onInitPersonEarElse--');
      }
    } catch (error) {
      // this.store.dispatch(ExpensivesPersonSuccess({ expensives: [], ref: 'ngOnInitCatch ExpensivesPerson' }));
      // console.log(error);
    }
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
    this.susA.unsubscribe();

    this.store.dispatch(ResetFiltersExpensives());
  }

  // Async function to perform execution of all promise
  async getAllData(): Promise<any> {
    await this.storeSrv.getAllDataExpensives();
  };

  async deleteEarning(item: IExpensive) {
    await this.storeSrv.deleteExpensive(item);
  }

  createUrl(params: any){
    this.utils.createUrl(params, this.actRoute, 'investments-person.component');
  }

  setPage(): void {
    this.store.dispatch(updateFiltersPaginatorExpensives({
      filters: { page: this.pageA },
      ref: 'setPage'
    }));
  }

  next(): void {
    this.page++;
    this.store.dispatch(updateFiltersPaginatorExpensives({
      filters: { page: this.page },
      ref: 'next'
    }));
  }

  prev(): void {
    this.page--;
    this.store.dispatch(updateFiltersPaginatorExpensives({
      filters: { page: this.page },
      ref: 'prev'
    }));
  }
}

