import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { ICategoryExpense } from 'src/app/interfaces/categories_expenses';
import { IExpensive } from 'src/app/interfaces/expensives';
import { IUserData } from 'src/app/interfaces/user';
import { ExpensivesService } from 'src/app/services/expensives.service';
import { StoreService } from 'src/app/services/store.service';
import { IState } from 'src/app/store';
import { CategoriesExpenses } from 'src/app/store/actions/categories_expenses';

@Component({
  selector: 'app-item-expensive',
  templateUrl: './item-expensive.component.html'
})
export class ItemExpensiveComponent implements OnInit, OnDestroy {
  @Input() showDelete: boolean = false;
  @Input() complete: boolean = false;
  @Input() item: any;
  @Input() link: any = true;
  @Input() listMode: any = false;
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  showForm: boolean = false;

  categories: Array<ICategoryExpense> = [];
  accounts: IAccount[] = [];
  user: IUserData = {};

  form: FormGroup = new FormGroup({});
  sus: Subscription = new Subscription();

  constructor(
    private expensivesService: ExpensivesService, 
    private store: Store<IState>, 
    private router: Router,
    private storeSrv: StoreService
  ) {
    this.sus = this.store.subscribe((state: IState) => {
      this.categories = state?.categories_expenses?.data;
      this.accounts = state?.user?.accounts;
      this.user = state?.user?.data;
    });
  }

  async ngOnInit() {
    this.form = new FormGroup({
      value: new FormControl(this.item?.value, Validators.required),
      description: new FormControl(this.item?.description, Validators.required),
      category: new FormControl(this.item?.category_expensives?.id, Validators.required),
      id_account: new FormControl(this.item?.account?.id, Validators.required),
      installments: new FormControl({value: this?.item?.installments, disabled: this?.item?.installments ? false : true}),
      fixed: new FormControl(this.item?.fixed),
      date: new FormControl(this.item?.date, Validators.required)
    });
    
    if(!this.categories?.length && localStorage.user){
      try {
        let response: any = await this.expensivesService.categoriesExpenses(JSON.parse(localStorage.user).person.id);

        if(response.length){
          this.store.dispatch(CategoriesExpenses({any: response}));
        }else{
          console.log('error en categories expensives');
        }
      } catch (error) { }
    }

    setTimeout(() => this.value?.nativeElement.focus(), 300);
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  seeDetail(): void{
    if(!this.link){
      return;
    }

    this.router.navigate(['/dashboard/expensives/', this.item.id, 'details']);
  }

  validateMethod(): void{
    const resultMethod: any = this.accounts.find(method => method.id === +this.form.value.account);

    if(resultMethod && resultMethod?.credit){
      this.form.controls.installments.enable();
    }else{
      this.form.controls.installments.setValue('');
      this.form.controls.installments.disable();
    }
  }

  editExpensive(): void{
    this.showForm = !this.showForm;
  }

  async deleteExpensive(item: IExpensive){
    await this.storeSrv.deleteExpensive(item);
  }

}
