import { Location } from '@angular/common';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Actions } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { ICategoryExpense } from 'src/app/interfaces/categories_expenses';
import { IStoreExpensives } from 'src/app/interfaces/expensives';
import { IUserData } from 'src/app/interfaces/user';
import { UtilsExpensivesService } from 'src/app/pages/dashboard/expensives/services/utils-expensives.service';
import { IState } from 'src/app/store';
import { resetPageFilters } from 'src/app/store/actions/expensives.actions';

@Component({
  selector: 'app-expensives-filters',
  templateUrl: './expensives-filters.component.html'
})
export class ExpensivesFiltersComponent implements OnInit, OnDestroy {
  @Input() form: FormGroup = new FormGroup({});
  @Input() isFromCreate: boolean = false;
  @Input() isFromGraphics: boolean = false;

  sub: Subscription = new Subscription();
  subA: Subscription = new Subscription();

  user:IUserData = {};

  quantity$: Observable<number> = this.store.select((state) => state.expensives.quantity);
  expensives$: Observable<IStoreExpensives> = this.store.select((state) => state.expensives);
  categories: Observable<ICategoryExpense[]> = this.store.select(state => state.categories_expenses.data);
  accounts: Observable<IAccount[]> = this.store.select(state => state.user.data.accounts);

  loading: boolean = false;
  loaded: boolean = false;

  queryParams: any;
  params: any;

  arrayItems: number[] = [4,8,12,16,20,24,28,36,48,60,100];
  arrayKeys: any[] = [
    {value: 'date', text: 'Date'},
    {value: 'value', text: 'Value'},
    {value: 'description', text: 'Description'},
    {value: 'id_category_earnings', text: 'Category'},
    {value: 'id_account', text: 'Account'},
  ];
  arrayTypes: any[] = [
    {value: 'DESC', text: 'Desc'},
    {value: 'ASC', text: 'Asc'},
  ];

  constructor(
    private store: Store<IState>,
    private utils: UtilsExpensivesService,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private actions: Actions<IState>
  ) {
    this.location.subscribe(async (x) => {
      if(x?.pop){
        await this.expensivesFilter();
      }
    });
    this.sub = this.store.select(state => state).subscribe(data => {
      try {
        this.user = data?.user?.data;
        this.queryParams = data?.expensives?.filters;
        this.loading = data?.expensives?.loading;
        this.loaded = data?.expensives?.loaded;
      } catch (error) {}
    });

    this.subA = this.actions.subscribe((action: any) => {
      // console.log('action', action);
      if(
        action.type === '[Expensives User] Set Filters Initial' || 
        action.type === '[Expensives User] Restore Filters Expensives'
      ){
        this.utils.setDataOnForm(this.form, this.queryParams);
      }

      if(action.type === '[Expensives User] Reset Filters Expensives'){
        this.form = this.utils.form();
      }

      if(action.type === '[Expensives User] Update Filters Paginator Expensives'){
        this.expensivesFilter(false);
      }
    });
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
    this.subA.unsubscribe();
  }

  async ngOnInit() {}

  async expensivesFilter(reset: boolean = true) {
    this.params = { ...this.queryParams, };

    if(this.queryParams.page > 0 && reset){
      this.store.dispatch(resetPageFilters())
      this.params.page = 0;
      this.params.pages = 0;
      this.params.pagesArray = [];
    }

    if(!this.form.value.price) { 
      delete this.form.value.price; 
      delete this.params.price; 
    }

    if(!this.form.value.search) { 
      delete this.form.value.search; 
      delete this.params.search; 
    }

    this.utils.expensivesFilter(this.params, this.activatedRoute, this.form);
  }
}
