import { Component, Input, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { IAccount } from 'src/app/interfaces/accounts';
import { ICategoryExpense } from 'src/app/interfaces/categories_expenses';
import { IState } from 'src/app/store';

@Component({
  selector: 'app-select-accounts',
  templateUrl: './select-accounts.component.html'
})
export class SelectAccountsComponent implements OnDestroy {
  @Input() from: string = '';
  @Input() showDetails: boolean = false;
  @Input() onlyDebits: boolean = false;
  @Input() placeholder: string = 'Account';
  @Input() formControlName: string = 'id_account';
  @Input() form: FormGroup = new FormGroup({});

  categories: Array<ICategoryExpense> = [];
  accounts: Array<IAccount> = [];

  sus: Subscription = new Subscription();

  constructor(
    private store: Store<IState>,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state: IState) => {
      this.categories = state?.categories_expenses?.data;
      this.accounts = state?.user?.accounts;
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  validateMethod(): void{
    if(!this.accounts?.length) return;
    const resultAccount: any = this.accounts.find(account => account.id === +this.form.value[this.formControlName]);
    
    if(this.from === 'expensives' ){
      if(resultAccount && resultAccount?.credit){
        this.form.controls.installments.enable();
        this.form.controls.installments.setValue(1);
      }else{
        this.form.controls.installments.setValue('');
        this.form.controls.installments.disable();
      }
    }
  }
  
  validateSelect(ref: string){
    const category: string = this.form.value[ref];
    const array: any = ref === 'category' ? this.categories : this.accounts;
    const url: any = ref === 'category' ? '/dashboard/'+this.from+'/categories/create' : '/dashboard/accounts/create';

    if(category === 'create' && !array?.length){
      this.router.navigateByUrl(url);
    }
  }

}
