import { Component, Input, OnInit } from '@angular/core';
import { ApexTitleSubtitle, ApexXAxis } from 'ng-apexcharts';
import { TType } from 'src/app/interfaces/charts';
import { GlobalService } from 'src/app/services/global.service';

@Component({
  selector: 'app-line',
  templateUrl: './line.component.html'
})
export class LineComponent implements OnInit {
  @Input() dataGraphics: any = [];
  @Input() id: string = '' + Math.floor(Math.random() * 1000);
  @Input() type: TType = 'all';
  @Input() title: ApexTitleSubtitle= {};
  @Input() nameLegend: string = '';
  @Input() nameAvg: string = '';
  @Input() height: number = 250;
  @Input() xAxis: ApexXAxis = {};

  public chartOptions: any;
  dataChart: any = [];

  constructor(private globalSrv: GlobalService) {}

  ngOnInit() {
    this.dataChart = (this.type === 'all'? 
            this.globalSrv.generateDataLine(this.nameLegend, this.dataGraphics) : (
              this.type === 'year' ?
                this.globalSrv.generateDataLine(this.nameLegend, this.dataGraphics, 'month') : (
                  this.type === 'month' ?
                    this.globalSrv.generateDataLine(this.nameLegend, this.dataGraphics, 'dia') : []
                  )
              )
            );
    
    // console.warn(this.dataChart);

    const defaultValuesLine: any = {
      series: this.dataChart?.result,
      title: this.title,
      height: this.height,
      nameAnnotation: this.nameAvg,
    };

    this.chartOptions = (this.type === 'all' || this.type === 'month' ? 
            this.globalSrv.dataChartLine(
              defaultValuesLine, 
              this.dataChart?.average,
            ) : (
              this.type === 'year' ?
                this.globalSrv.dataChartLine(
                  {
                    ...defaultValuesLine,
                    xaxis: this.xAxis
                  }, 
                  this.dataChart?.average,
                ) : []
            )
          );
  }

}
