import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IExpensive, IExpensiveCategory } from 'src/app/interfaces/expensives';
import { ICreateExpensivesResponse } from 'src/app/interfaces/http/expensives-http.type';
import { IUserData } from 'src/app/interfaces/user';
import { ExpensivesService } from 'src/app/services/expensives.service';
import { IState } from 'src/app/store';
import { CreateExpensivesSuccess, ExpensivesReset } from 'src/app/store/actions/expensives.actions';
import { currentDateInput, dialogSmall } from 'src/environments/global';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-form-expensives',
  templateUrl: './form-expensives.component.html'
})
export class FormExpensivesComponent implements OnInit, OnDestroy {
  @Input() expensive: IExpensive = {};
  @ViewChild('value', { static : true }) value: ElementRef | undefined;
  form: FormGroup = new FormGroup({});
  date: string = currentDateInput();
  user: IUserData = {};
  categories: IExpensiveCategory[] = [];
  categories$: Observable<IExpensiveCategory[]> = this.store.select((state) => state.categories_expenses.data);

  sus: Subscription = new Subscription();

  constructor(
    private expensivesSrv: ExpensivesService, 
    private store: Store<IState>,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state) => {
      this.categories = state.categories_expenses.data;
      this.user = state.user.data;
    });
  }

  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  ngOnInit() {
    this.form = new FormGroup({
      id: new FormControl(this.expensive?.id),
      value: new FormControl(this.expensive?.value, Validators.required),
      description: new FormControl(this.expensive?.description, Validators.required),
      id_category_expensives: new FormControl(this.expensive?.category_expensives?.id, Validators.required),
      id_account: new FormControl(this.expensive?.account?.id, Validators.required),
      date: new FormControl(this.expensive?.date || this.date, Validators.required),
      fixed: new FormControl(this.expensive?.fixed),
      installments: new FormControl({value: this.expensive.installments, disabled: true}),
    });

    if(this.expensive.installments){
      this.form.controls.installments.enable();
    }

    setTimeout(() => this.value?.nativeElement.focus(), 300);
  }

  async createOrUpdateExpensive() {
    if (this.form.valid) {
      let { date } = this.form.value;

      try {
        let data = {
          ...this.form.value,
          date: new Date(date),
          id_person: this.user?.person?.id,
        };
        let response: ICreateExpensivesResponse = await this.expensivesSrv.createUpdateExpense(''+this.expensive.id, data);

        if (response.expensive) {
          if(!this.expensive.id){
            // console.log('registro exitoso');
            this.form.reset();
            this.form.controls.date.setValue(this.date);
  
            this.value?.nativeElement.focus();
  
            if(response.expensives){
              dialogSmall( 'Expensive Created Succesfully!', 'success');
              this.store.dispatch(CreateExpensivesSuccess());
            }
          }else{
            this.store.dispatch(ExpensivesReset());
            if(response?.status === 1) {
              dialogSmall('Expensive Updated Succesfully!')
            }
            this.router.navigateByUrl('/dashboard/expensives');
          }
        } else {
          // console.log('registro fallido');
        }
      } catch (error: any) {
        console.log('error => ', error?.error);
        dialogSmall(`${error.error}`, 'error');
      }
    }
  }

  validateSelect(ref: string){
    const id_category_expensives: string = this.form.value[ref];
    const url: any = ref === 'id_category_expensives' ? '/dashboard/expensives/categories/create' : '/dashboard/accounts/create';
    if(id_category_expensives === 'create' && !this.categories?.length){
      this.router.navigateByUrl(url);
    }
  }

}
