import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { StoreService } from 'src/app/services/store.service';
import { IState } from 'src/app/store';
import { SetFiltersExpensives } from 'src/app/store/actions/expensives.actions';
import { currentDateInput } from 'src/environments/global';

@Injectable({
  providedIn: 'root'
})
export class UtilsExpensivesService {

  constructor(
    private storeSrv: StoreService,
    private store: Store<IState>,
    private router: Router,
  ) { }

  form(): FormGroup {
    const yearValue = new Date().getFullYear();
    const monthValue = new Date().getMonth() + 1;
    const currentDate = new Date();
    const result = currentDate.setDate(currentDate.getDate() + 7);
    const date = currentDateInput(new Date());
    const dateT = currentDateInput(new Date(result));

    return new FormGroup({
      filter: new FormControl('', Validators.required),
      month: new FormControl(`${yearValue}-${monthValue < 10 ? `0${monthValue}` : monthValue}`, Validators.required),
      date: new FormControl(date, Validators.required),
      date1: new FormControl(date, Validators.required),
      date2: new FormControl(dateT, Validators.required),
      week: new FormControl('', Validators.required),
      year: new FormControl(yearValue, Validators.required),
      notincategory: new FormControl('', Validators.required),
      notinaccount: new FormControl('', Validators.required),
      limit: new FormControl(20),
      orderKey: new FormControl('date'),
      orderType: new FormControl('DESC'),
      price: new FormControl(),
      search: new FormControl(),
      fixed: new FormControl(),
      transfers: new FormControl(),
      atm: new FormControl(),
      installments: new FormControl(),

      id_account: new FormControl('', Validators.required),
      category: new FormControl('', Validators.required),
    });
  }

  createUrl(params: any, activatedRoute: ActivatedRoute, ref: string){
    // console.log(this.router.url.includes('expensives'));
    if(params.pagesArray) delete params.pagesArray;
    if(params.pages) delete params.pages;
    if(params.total) delete params.total;
    if(params.quantity) delete params.quantity;

    if(this.router.url.includes('expensives')) {
      // console.error(this.router.url);
      // console.log('createUrl (event)', ref, params);

      this.router.navigate([], {
        relativeTo: activatedRoute,
        queryParams: {...params},
        // queryParamsHandling: ''
      });
    }
  }

  async expensivesFilter(params: any, activatedRoute: ActivatedRoute, form: FormGroup) {
    let dataForm = {...form.value};

    if(dataForm.category){ params.category = dataForm.category ? dataForm.category.join(',') : null; }
    if(dataForm.notincategory){ params.notincategory = dataForm.notincategory ? dataForm.notincategory.join(',') : null; }
    if(dataForm.notinaccount){ params.notinaccount = dataForm.notinaccount ? dataForm.notinaccount.join(',') : null; }
    if(dataForm.id_account){ params.id_account = dataForm.id_account ? dataForm.id_account.join(',') : null; }
    if(dataForm.limit){ params.limit = dataForm.limit; }
    if(dataForm.orderKey){ params.orderKey = dataForm.orderKey; }
    if(dataForm.orderType){ params.orderType = dataForm.orderType; }
    if(dataForm.price){ params.price = dataForm.price; }
    if(dataForm.search){ params.search = dataForm.search; }
    if(dataForm.transfers){ params.transfers = dataForm.transfers; }
    if(dataForm.fixed){ params.fixed = dataForm.fixed; }
    if(dataForm.atm){ params.atm = dataForm.atm; }
    if(dataForm.installments){ params.installments = dataForm.installments; }

    if(params.category === '') delete params.category;
    if(params.notincategory === '') delete params.notincategory;
    if(params.notinaccount === '') delete params.notinaccount;
    if(params.id_account === '') delete params.id_account;
    if(params.limit === '') delete params.limit;
    if(params.orderKey === '') delete params.orderKey;
    if(params.orderType === '') delete params.orderType;
    if(params.price === '') delete params.price;
    if(!params.price) delete params.price;
    if(params.search === '') delete params.search;
    if(!params.search) delete params.search;
    if(params.transfers === '' || !form.value.transfers) delete params.transfers;
    if(params.atm === '' || !form.value.atm) delete params.atm;
    if(params.fixed === '' || !form.value.fixed) delete params.fixed;
    if(params.installments === '' || !form.value.installments) delete params.installments;

    if(dataForm.filter) params.type = dataForm.filter;

    if(dataForm.filter === 'today'){
      // clean others fields if exists
      if(params.month) delete params.month;
      if(params.year) delete params.year;
      if(params.date1) delete params.date1;
      if(params.date2) delete params.date2;
      if(params.date) delete params.date;

      params = { ...params };
    }else if(dataForm.filter === 'year' && dataForm.year){
      // clean others fields if exists
      if(params.month) delete params.month;
      if(params.date1) delete params.date1;
      if(params.date2) delete params.date2;
      if(params.date) delete params.date;
      params = {
        ...params,
        year: dataForm.year,
      };
    }else if(dataForm.filter === 'month' && dataForm.month){
      // clean others fields if exists
      if(params.year) delete params.year;
      if(params.date1) delete params.date1;
      if(params.date2) delete params.date2;
      if(params.date) delete params.date;
      params = {
        ...params,
        month: dataForm.month,
      };
    }else if(dataForm.filter === 'dates' && dataForm.date1 && dataForm.date2){
      // clean others fields if exists
      if(params.month) delete params.month;
      if(params.year) delete params.year;
      if(params.date) delete params.date;
      params = {
        ...params,
        date1: dataForm.date1,
        date2: dataForm.date2,
      };
    }else if(dataForm.filter === 'date' && dataForm.date){
      // clean others fields if exists
      if(params.month) delete params.month;
      if(params.year) delete params.year;
      if(params.date1) delete params.date1;
      if(params.date2) delete params.date2;
      params = {
        ...params,
        date: dataForm.date,
      };
    }else if(dataForm.filter === 'week' && dataForm.week){
      params = {
        ...params,
        type: 'date',
        week: dataForm.week,
      };
    }else{
      // clean all fields if exists
      if(params.type) delete params.type;
      if(params.month) delete params.month;
      if(params.year) delete params.year;
      if(params.date1) delete params.date1;
      if(params.date2) delete params.date2;
      if(params.date) delete params.date;
      if(
        dataForm.category || 
        dataForm.notincategory || 
        dataForm.notinaccount || 
        dataForm.limit || 
        dataForm.orderKey || 
        dataForm.orderType || 
        dataForm.price || 
        dataForm.search || 
        dataForm.atm || 
        dataForm.fixed || 
        dataForm.installments || 
        dataForm.transfers || 
        dataForm.id_account
      ){
        params = { ...params };
      }
    }
    this.setDataOnForm(form, params, 'expensivesFilter()');
    await this.manageService(params, activatedRoute, !dataForm.filter?'normal filters -----------------':dataForm.filter);

    if(!params.category) delete params.category;
    if(!params.notincategory) delete params.notincategory;
    if(!params.notinaccount) delete params.notinaccount;
    if(!params.id_account) delete params.id_account;
    if(!params.limit) delete params.limit;
    if(!params.orderKey) delete params.orderKey;
    if(!params.orderType) delete params.orderType;
  }

  async manageService(params: any, activatedRoute: ActivatedRoute, ref?: string){
    // console.log('event ManageService() ', params, ref);
    this.store.dispatch(SetFiltersExpensives({filters: {...params}, ref: 'manageService Filters UTILS'}));
    this.createUrl(params, activatedRoute, 'expensivesFilterManage()');
    await this.storeSrv.expensivesByDate(params, ref+'filters');
  }

  setDataOnForm(form: FormGroup, params: any, ref?: string){
    // console.warn('start event setDataOnForm()', ref);
    if(this.router.url.includes('expensives')){
      if(!Object.keys(form.value).length){
        // console.error('form has no values****');
        return;
      }
      // console.log('event setDataOnForm() ', params, form);
      form.controls.filter.setValue(params?.type);
      if(params?.type === 'year'){
        form.controls.year.setValue(params?.year);
      }
      if(params?.type === 'month'){
        form.controls.month.setValue(params?.month);
      }
      if(params?.type === 'dates'){
        form.controls.date1.setValue(params?.date1);
        form.controls.date2.setValue(params?.date2);
      }
      if(params?.type === 'date'){
        form.controls.date.setValue(params?.date);
      }
      if(params?.type === 'week'){
        form.controls.week.setValue(params?.week);
      } 

      if(params?.category){
        form.controls.category.setValue(params?.category.split(',').map((i: any) => parseInt(i)));
      }
      if(params?.notincategory){
        form.controls.notincategory.setValue(params?.notincategory.split(',').map((i: any) => parseInt(i)));
      }
      if(params?.notinaccount){
        form.controls.notinaccount.setValue(params?.notinaccount.split(',').map((i: any) => parseInt(i)));
      }
      if(params?.id_account){
        form.controls.id_account.setValue(params?.id_account.split(',').map((i: any) => parseInt(i)));
      }
      if(params?.limit){
        form.controls.limit.setValue(params?.limit);
      }
      if(params?.orderKey){
        form.controls.orderKey.setValue(params?.orderKey);
      }
      if(params?.orderType){
        form.controls.orderType.setValue(params?.orderType);
      }
      if(params?.price){
        form.controls.price.setValue(params?.price);
      }
      if(params?.search){
        form.controls.search.setValue(params?.search);
      }
      if(params?.atm && form.value.atm){
        form.controls.atm.setValue(params?.atm || form.value.atm);
      }else if(form.value.atm || params?.atm){
        form.controls.atm.setValue(form.value.atm || params.atm);
      }else{
        form.controls.atm.setValue(null);
      }
      if(params?.transfers && form.value.transfers){
        form.controls.transfers.setValue(params?.transfers || form.value.transfers);
      }else if(form.value.transfers || params?.transfers){
        form.controls.transfers.setValue(form.value.transfers || params.transfers);
      }else{
        form.controls.transfers.setValue(null);
      }

      if(params?.fixed && form.value.fixed){
        form.controls.fixed.setValue(params?.fixed || form.value.fixed);
      }else if(form.value.fixed || params?.fixed){
        form.controls.fixed.setValue(form.value.fixed || params.fixed);
      }else{
        form.controls.fixed.setValue(null);
      }

      if(params?.installments && form.value.installments){
        form.controls.installments.setValue(params?.installments || form.value.installments);
      }else if(form.value.installments || params?.installments){
        form.controls.installments.setValue(form.value.installments || params.installments);
      }else{
        form.controls.installments.setValue(null);
      }
    }
  }
}
