import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { ICategoryEarning } from 'src/app/interfaces/categories_earnings';
import { IUserData } from 'src/app/interfaces/user';
import { EarningsService } from 'src/app/services/earnings.service';
import { IState } from 'src/app/store';
import { CategoriesEarnings } from 'src/app/store/actions/categories_earnings';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-list-categories-earnings',
  templateUrl: './list.component.html'
})
export class ListCategoriesEarningsComponent implements OnInit, OnDestroy {
  @Input() padding: boolean = true;
  @Input() complete: boolean = true;
  categories: Observable<any[]> = this.store.select(state => state.categories_earnings.data);
  categoriesGraphic: any[] = [];
  user:IUserData = {};
  public chartOptions: any;
  dataChart: any = [];

  sub: Subscription = new Subscription();

  constructor(
    private earningsSrv: EarningsService,
    private store: Store<IState>
  ) {
    this.sub = this.store.select(state => state).subscribe(data => {
      try {
        this.user = data?.user?.data;
      } catch (error) { console.log(error); }
    });
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

  async ngOnInit() {
    if(this.complete){
      await this.getDataCategories();
    }
  }

  async getDataCategories(){
    try {
      let response: any = await this.earningsSrv.getValuesDataCategories(''+this.user.id);

      if(response.categories){
        this.categoriesGraphic = [{}, ...response.categories].map(i => {
          if(i.total) i.total = parseInt(i.total);
          return i;
        });
      }
    } catch (error) { }
  }

  async deleteCategory(item: ICategoryEarning){
    // return;
    if(item.id){
      try {
        let data = {
          category: item.id,
          person: item.id_person
        };
        let response: any = await this.earningsSrv.deleteCategorieEarning(data);

        if(response.category){
          console.log('categoria eliminada exitosamente');
          // this.form.reset();
          this.store.dispatch(CategoriesEarnings({any: response.categories}));
        }else{
          console.log('categoria eliminada fallida');
        }
      } catch (error: any) {
        console.log('error => ', error);
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: 'Error to delete category',
          footer: error?.error?.message
        });
      }

    }
  }

}
