import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { StoreService } from 'src/app/services/store.service';
import { IState } from 'src/app/store';
import { LogOut } from 'src/app/store/actions/user';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html'
})
export class DashboardComponent implements OnInit {
  user: Observable<any> = this.store.select(state => state.user.data);
  isOpenMenu: boolean = false;
  isStaticMenu: boolean = false;

  constructor(
    private store: Store<IState>, 
    private storeSrv: StoreService, 
    private router:Router
  ) { }

  async ngOnInit(): Promise<void> {
    if (localStorage.isStaticMenu) {
      this.isOpenMenu = JSON.parse(localStorage.isStaticMenu);
      delete localStorage.isOpenMenu;
    }else{
      if (localStorage.isOpenMenu) {
        this.isOpenMenu = JSON.parse(localStorage.isOpenMenu);
      }
    }

    await this.storeSrv.getAccountsUser();
  }

  logout(){
    this.store.dispatch(LogOut());
    this.router.navigateByUrl('/login');
  }

  nav(ref?: string){
    if(ref === 'static'){
      if(!this.isOpenMenu){
        localStorage.isStaticMenu = JSON.stringify(true);
        delete localStorage.isOpenMenu;
      }else{
        delete localStorage.isStaticMenu;
      }

      this.isOpenMenu = !this.isOpenMenu;
      return;
    }
  }

  get year(): string{
    return '' + new Date().getFullYear();
  }
}
