import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Actions } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { IState } from 'src/app/store';
import { IColsWrap } from '../heritage-person/heritage-person.component';
import { IInvestment, IStoreInvestments } from 'src/app/interfaces/investments';
import { IUserData } from 'src/app/interfaces/user';
import { StoreService } from 'src/app/services/store.service';
import { ResetFiltersInvestments, RestoreFiltersInvestments, updateFiltersPaginatorInvestments } from 'src/app/store/actions/investments.actions';
import { UtilsInvestmentsService } from 'src/app/pages/dashboard/investments/services/utils-investments.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-investments-person',
  templateUrl: './investments-person.component.html',
  styles: [],
})
export class InvestmentsPersonComponent implements OnInit, OnDestroy {
  @Input() showHeader: boolean = true;
  @Input() showLink: boolean = true;
  @Input() showDelete: boolean = false;
  @Input() padding: boolean = true;
  @Input() limit: number = 0;
  @Input() cols: IColsWrap = {
    xl: 4,
    md: 3,
    sm: 2,
  };
  user: IUserData = {};

  pages: number = 0;
  pagesArray: any[] = [];
  limit$: number = 0;
  page: number = 0;
  pageA: number = 0;
  quantity: number = 0;
  investments$: Observable<IStoreInvestments> = this.store.select((state) => state.investments);
  loading: boolean = false;
  loaded: boolean = false;

  sus: Subscription = new Subscription();
  susA: Subscription = new Subscription();

  paramsRouter: any = {};
  params: any = {};
  validUrl: boolean = true;

  constructor(
    private store: Store<IState>,
    private storeSrv: StoreService,
    private actions: Actions<IState>,
    private utils: UtilsInvestmentsService,
    private router: Router,
    private actRoute: ActivatedRoute
  ) {
    this.susA = this.actions.subscribe((action: any) => {
      // console.log('action', action);
      if(action.type === '[User] InvestmentsPerson'){
        this.getAllData();
      }
    });

    this.sus = this.store.select((state) => state).subscribe((state) => {
      this.user = state?.user?.data;
      this.loading = state?.investments?.loading;
      this.loaded = state?.investments?.loaded;
      this.page = state?.investments?.filters?.page;
      this.pageA = state?.investments?.filters?.page;
      this.pages = state?.investments?.filters?.pages;
      this.pagesArray = state?.investments?.filters?.pagesArray;
      this.limit$ = state?.investments?.filters?.limit;
      this.quantity = state?.investments?.filters?.quantity;
      
      if(Object.keys(state?.investments?.filters).length){
        this.params = state?.investments?.filters;
      }
    });
  }

  async getAllData(): Promise<any> {
    await this.storeSrv.getAllDataInvestments();
  };

  ngOnDestroy(): void {
    this.sus.unsubscribe();
    this.susA.unsubscribe();

    this.store.dispatch(ResetFiltersInvestments());
  }

  async ngOnInit() {
    if(Object.keys(this.paramsRouter).length){
      // console.log(this.paramsRouter);
      this.store.dispatch(RestoreFiltersInvestments({ filters: this.paramsRouter }));
    }
    await this.getAllData();
    try {
      if(this.router.url.indexOf('investments/create') >= 0 && Object.keys(this.params).length){
        return;
      } else if(!this.router.url.includes('create')){
        await this.storeSrv.investmentsByDate(this.paramsRouter, 'onInitPersonInv');
        return;
      }else{
        await this.storeSrv.investmentsByDate({}, '---onInitPersonInvElse--');
      }
    } catch (error) {
      // this.store.dispatch(InvestmentsPersonSuccess({ investments: [], ref: 'ngOnInitCatch InvestmentsPerson' }));
      // console.log(error);
    }
  }

  async deleteEarning(item: IInvestment) {
    await this.storeSrv.deleteInvestment(item);
  }

  createUrl(params: any){
    this.utils.createUrl(params, this.actRoute, 'earnings-person.component');
  }

  setPage(): void {
    this.store.dispatch(updateFiltersPaginatorInvestments({
      filters: { page: this.pageA },
      ref: 'setPage'
    }));
  }

  next(): void {
    this.page++;
    this.store.dispatch(updateFiltersPaginatorInvestments({
      filters: { page: this.page },
      ref: 'next'
    }));
  }

  prev(): void {
    this.page--;
    this.store.dispatch(updateFiltersPaginatorInvestments({
      filters: { page: this.page },
      ref: 'prev'
    }));
  }
}