import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { ExpensivesService } from 'src/app/services/expensives.service';
import { UserService } from 'src/app/services/user.service';
import { IState } from 'src/app/store';
import { LogOut } from 'src/app/store/actions/user';

let dollarUSLocale = Intl.NumberFormat('en-US');

@Component({
  selector: 'app-start',
  templateUrl: './start.component.html',
})
export class StartComponent implements OnInit {
  user: Observable<any> = this.store.select((state) => state.user.data);
  options: any;
  options2: any;
  type: string = 'week';

  constructor(
    private store: Store<IState>, 
    private router: Router, 
    private userService:UserService,
    private expensivesService:ExpensivesService,
  ) {
    this.options = {
      series: [],
      chart: {
        type: 'donut',
        height: 500,
        // with: 300
      },
      plotOptions: {
        pie: {
          donut: {
            labels: {
              show: true,
            },
            size: '50%',
          },
        },
      },
      dataLabels: {
        enabled: true,
        offset: 80,
      },
      stroke: {
        show: true,
        width: 2,
        colors: ['transparent'],
      },
      xaxis: {
        // type: 'datetime',
        categories: [
          'Lunes',
          'Martes',
          'Miercoles',
          'Jueves',
          'Viernes',
          'Sabado',
          'Domingo',
        ],
      },
      yaxis: {
        // title: {
        //   text: '$ (thousands)',
        // },
      },
      fill: {
        opacity: 1,
      },
      tooltip: {
        // y: {
        //   formatter: function (val: any) {
        //     return '$ ' + val + ' thousands';
        //   },
        // },
      },
      labels: [],
    };
    this.options2 = {
      series: [],
      chart: {
        type: 'treemap',
        height: 1500,
        // with: 300
      },
      legend: {
        show: true
      },
      plotOptions: {
        treemap: {
          distributed: true,
          // shadeIntensity: 0.5,
          // reverseNegativeShade: true,
          // colorScale: {
          //   ranges: [
          //     {
          //       from: -6,
          //       to: 0,
          //       color: '#f60',
          //     },
          //     {
          //       from: 0.001,
          //       to: 6,
          //       color: '#52B12C',
          //     },
          //   ],
          // },
        },
      },
      dataLabels: {
        enabled: true,
        style: {
          fontSize: '16px',
          color: '#333'
        },
        formatter: function (text: any, op: any) {
          return [text, dollarUSLocale.format(op.value)];
        },
        offsetY: -4,
      },
      stroke: {
        show: true,
        width: 2,
        colors: ['transparent'],
      },
      xaxis: {
        // type: 'datetime',
        categories: [
          'Lunes',
          'Martes',
          'Miercoles',
          'Jueves',
          'Viernes',
          'Sabado',
          'Domingo',
        ],
      },
      yaxis: {
        // title: {
        //   text: '$ (thousands)',
        // },
      },
      fill: {
        // opacity: 1,
      },
      tooltip: {
        // y: {
        //   formatter: function (val: any) {
        //     return '$ ' + val + ' thousands';
        //   },
        // },
      },
      labels: [],
    };
  }

  async getDolarPrice(): Promise<any>{
    try{
      let res: any = await this.userService.getDolar();

      console.log(res);
    }catch(e){
      console.log(e);
    }
  }

  async ngOnInit() {
    // this.getDolarPrice();
    try {
      console.log('*********************************');
      console.log('ngOnInit start');
      console.log('*********************************');
      let response: any = await this.expensivesService.getExpensesByDate({
        type: this.type,
        person: JSON.parse(localStorage.user).person.id,
      }, 'onInit');
  
      // console.log(response);
      if(response.total){
        this.options.title = {
          text: `Total last 7 days $${dollarUSLocale.format(response.total)}`,
        };
      }
      if(response.expensives){
        // console.log(this.generateData(response.expensives));
        // console.log(this.generateLabels(response.expensives));
        this.options.labels = this.generateLabels(response.expensives);
        this.options.series = this.generateData(response.expensives);
        this.options2.series = this.generateData2(response.expensives);
      }
    } catch (error) {
      // console.log('error en expensives por fecha');
    }
  }

  generateData(data: any): any{
    // console.warn(data);
    let arr: any = [];
    data.map((item: any) => {
      arr.push(parseInt(item.value));
    });
    return arr;
  }
  
  generateData2(data: any): any{
    // console.warn(data);
    let arr: any = [];
    data.map((item: any) => {
      arr.push({
        x: item.category_expensives.name,
        y: parseInt(item.value)
      });
    });
    return [
      { data: arr }
    ];
  }

  generateLabels(data: any): any{
    let arr: any = [];
    data.map((item: any) => {
      arr.push(item.category_expensives.name);
    });
    return arr;
  }

  logout() {
    this.store.dispatch(LogOut());
    this.router.navigateByUrl('/login');
  }
}
