import { Injectable } from '@angular/core';
import { IChartItem, IChartOptions } from '../interfaces/charts';
import { ApexAnnotations, ApexDataLabels, ApexGrid, ApexLegend, ApexPlotOptions, ApexStroke, ApexTitleSubtitle, ApexTooltip, ApexXAxis, ApexYAxis } from 'ng-apexcharts';
import { monthsNames } from 'src/environments/global';

@Injectable({
  providedIn: 'root'
})
export class GlobalService {

  constructor() { }

  generateDataBar(name:string, data: any) {
    if (!data.length) return [];

    var result: any = [];

    [...data].reduce((res: any, value: any) => {
      let t: any = value?.name;

      if (!res[t]) {
        res[t] = {t};

        // Inicializamos a 0 el valor de cada key
        Object.keys(value).forEach(function (key) {
          if (key != 'date') {
            res[t][key] = 0;
          }
        })

        result.push(res[t]);
      }

      // Sumamos el valor de cada clave dentro de un bucle
      Object.keys(value).forEach(function (key) {
        if (key != 'date') {
          res[t][key] += value[key];
        }
      })

      return res;
    });
    

    const dataF: IChartItem[] = [...result].map((expensive: any) => {
      return {
        x: expensive.t,
        y: expensive.total
      };
    });

    const average = [...dataF].reduce((a: any, b: any) => a + b.y, 0) / dataF.length;
    let finalData: any = {
      result: [{
        name,
        data: dataF
      }], 
      average
    };

    return finalData;
  }

  generateDataLine(name:string, data: any, k: string = 'anio') {
    if (!data.length) return [];

    // sort data
    let dataForReduce = [...data].sort(
      (objA: any, objB: any) => 
        Number(new Date(objA.date)) - Number(new Date(objB.date)
      )
    );

    var result: any = [];
    var months: string[] = monthsNames;

    dataForReduce.reduce((res: any, value: any) => {
      let pD = value.date.split('-');
      let t: any = '';
      if(k === 'anio'){
        t = new Date(`${pD[1]}-${pD[2]}-${pD[0]}`).getFullYear();
      }
      if(k === 'month'){
        t = new Date(`${pD[1]}-${pD[2]}-${pD[0]}`).getMonth();
        t = months[t];
      }
      if(k === 'dia'){
        t = value.date.split('-')[2];
      }

      if (!res[t]) {
        res[t] = { [k]: t };

        // Inicializamos a 0 el valor de cada key
        Object.keys(value).forEach(function (key) {
          if (key != 'date') {
            res[t][key] = 0;
          }
        })

        result.push(res[t]);
      }

      // Sumamos el valor de cada clave dentro de un bucle
      Object.keys(value).forEach(function (key) {
        if (key != 'date') {
          res[t][key] += value[key];
        }
      })

      return res;
    });

    const dataF: IChartItem[] = [...result].map((expensive: any) => {
      return {
        x: expensive[k],
        y: expensive.value
      };
    });

    const average = [...dataF].reduce((a: any, b: any) => a + b.y, 0) / dataF.length;
    let finalData: any = {
      result: [{
        name,
        data: dataF
      }], 
      average
    };

    if(k === 'anio'){
      finalData.years = [...dataF].map((item: any) => item.x)
    }
    if(k === 'month'){
      finalData.months = [...dataF].map((item: any) => item.x)
    }
    if(k === 'dia'){
      finalData.days = [...dataF].map((item: any) => item.x)
    }

    return finalData;
  }

  generateDataDonut(name:string, data: any, k: string = 'account') {
    if(!data.length) return [];

    // sort data
    let dataForReduce = [...data].sort(
      (objA: any, objB: any) => 
        Number(new Date(objA.date)) - Number(new Date(objB.date)
      )
    );

    var result: any = [];
    dataForReduce.reduce((res: any, value: any) => {
      let t = '';
      if(k === 'account'){
        t = value.account.name;
      }else{
        t = value[`category_${k}`].name;
      }

      if (!res[t]) {
        res[t] = {t};

        // Inicializamos a 0 el valor de cada key
        Object.keys(value).forEach(function(key) {
            if(key != 'date'){
              res[t][key] = 0;
            }
        })

        result.push(res[t]);
      }

      // Sumamos el valor de cada clave dentro de un bucle
      Object.keys(value).forEach(function(key) {
        if(key != 'date'){
          res[t][key] += value[key];
        }
      })

      return res;
    });
    const values: IChartItem[] = result.map((expensive: any) => {
      return {
        x: expensive.t,
        y: expensive.value
      };
    });
    return values;
  }

  dataChartBar(data: any, avg: number): any {
    const defaultYAxis: ApexYAxis = {
      labels: {
        formatter: function (value: any) {
          return "$" + new Intl.NumberFormat().format(value);
        }
      },
    };

    const d = data?.series[0]?.data.map((i: any) => i?.y);

    return {
      series: [
        {
          name: "basic",
          data: d
        }
      ],
      chart: {
        type: "bar",
        height: 350
      },
      plotOptions: {
        bar: {
          horizontal: true
        }
      },
      dataLabels: {
        enabled: false
      },
      xaxis: {
        categories: data?.series[0]?.data.map((i: any) => i?.x)
      },
      yaxis: data?.yaxis || defaultYAxis,
    };
  }

  dataChartLine(data: IChartOptions, avg: number): IChartOptions {
    const defaultHeight = 300;
    const defaultDataLabels: ApexDataLabels = {
      enabled: true,
      formatter: (val: number): string => '$' + new Intl.NumberFormat().format(val)
    }; 
    const defaultStroke: ApexStroke = {
      curve: 'straight',
      width: 2
    };
    const defaultTitle: ApexTitleSubtitle = {
      text: "Chart Line",
      align: "center",
    };
    const defaultSubtitle: ApexTitleSubtitle = {};
    const defaultGrid: ApexGrid = {
      show: true,
      borderColor: '#ddd',
      yaxis: {
        lines: {
          show: true
        }
      },
      xaxis: {
        lines: {
          show: true
        }
      },
    };
    const defaultAnnotations: ApexAnnotations = {
      yaxis: [
        {
          y: avg,
          y2: avg,
          borderColor: '#000',
          fillColor: '#FEB019',
          label: {
            borderColor: '#FEB019',
            text: data?.nameAnnotation +' $' + new Intl.NumberFormat().format(avg),
            style: {
              color: '#fff',
              background: '#FEB019',
            }
          }
        }
      ]
    };
    const defaultXAxis: ApexXAxis = {
      labels: {
        formatter: function (value: any) {
          return new Intl.NumberFormat().format(value);
        }
      }
    };
    const defaultYAxis: ApexYAxis = {
      labels: {
        formatter: function (value: any) {

          return "$" + new Intl.NumberFormat().format(value);
        }
      },
    };

    return {
      series: data?.series,
      chart: {
        height: data?.height || defaultHeight,
        type: "line",
        zoom: {
          enabled: true
        }
      },
      dataLabels: data?.dataLabels || defaultDataLabels,
      annotations: data?.annotations || defaultAnnotations,
      stroke: data?.stroke || defaultStroke,
      title: data?.title || defaultTitle,
      subtitle: data?.subtitle || defaultSubtitle,
      yaxis: data?.yaxis || defaultYAxis,
      xaxis: data?.xaxis || defaultXAxis,
      grid: data?.grid || defaultGrid
    };
  }
  
  dataChartDonut(data: IChartOptions, avg: number): IChartOptions {
    const defaultHeight = 800;
    const defaultDataLabels: string[] = data?.labels || []; 
    const defaultTitle: ApexTitleSubtitle = {
      text: "Chart Donut",
      align: "center",
    };
    const defaultSubtitle: ApexTitleSubtitle = {};
    const defaultPlotOptions: ApexPlotOptions  = {
      pie: {
        donut: {
          labels: {
            show: true,
            total: {
              showAlways: true,
              show: true
            }
          }
        }
      }
    };
    const defaultLegend: ApexLegend = {
      position: 'bottom'
    };
    const defaultTooltip: ApexTooltip = {
      theme: "dark",
      x: {},
      y: {
        title: {
          formatter: (val: any) => {
            console.log(val);
            return "";
          }
        }
      }
    };

    return {
      series: data?.series,
      chart: {
        height: data?.height || defaultHeight,
        type: "pie",
        zoom: {
          enabled: true
        },
        toolbar: {
          show: true
        },
      },
      labels: data?.labels || defaultDataLabels,
      plotOptions: data?.plotOptions || defaultPlotOptions,
      legend: data?.legend || defaultLegend,
      title: data?.title || defaultTitle,
      subtitle: data?.subtitle || defaultSubtitle,
      tooltip: data?.tooltip || defaultTooltip
    };
  }

}
